import * as React from 'react'
import './chart.scss'
import * as d3 from 'd3'
import { useRef } from 'react'
import { cyclingData } from './data'

export interface ChartProps {
    data?: any
}

export interface Data {
    Time: Date
    Place: number
    Seconds: number
    Name: string
    Year: number
    Nationality: string
    Doping: string
    URL: string
}

const Chart: React.FC<ChartProps> = props => {
    const d3Container = useRef(null)

    const dataset = cyclingData
    var margin = {
            top: 100,
            right: 20,
            bottom: 30,
            left: 60,
        },
        width = 920 - margin.left - margin.right,
        height = 630 - margin.top - margin.bottom

    var x = d3.scaleLinear().range([0, width])

    var y = d3.scaleTime().range([0, height])

    var color = d3.scaleOrdinal(d3.schemeCategory10)

    var timeFormat = d3.timeFormat('%M:%S') as any
    var xAxis = d3.axisBottom(x).tickFormat(d3.format('d'))

    var yAxis = d3.axisLeft(y).tickFormat(timeFormat)

    var div = d3
        .select('body')
        .append('div')
        .attr('class', 'tooltip')
        .attr('id', 'tooltip')
        .style('opacity', 0)

    var svg = d3
        .select('body')
        .append('svg')
        .attr('width', width + margin.left + margin.right)
        .attr('height', height + margin.top + margin.bottom)
        .attr('class', 'graph')
        .append('g')
        .attr('transform', 'translate(' + margin.left + ',' + margin.top + ')')

    if (dataset) {
        const newData: Array<Data> = []

        dataset.forEach(function(d) {
            const place = +d.Place
            const parsedTime = d.Time.split(':').map(x => +x)
            const time = new Date(1970, 0, 1, 0, parsedTime[0], parsedTime[1])
            newData.push({
                Time: time,
                Place: place,
                Seconds: d.Seconds,
                Name: d.Name,
                Year: d.Year,
                Nationality: d.Nationality,
                Doping: d.Doping,
                URL: d.URL,
            })
        })

        x.domain([
            d3.min(newData, function(d) {
                return d.Year - 1
            }),
            d3.max(newData, function(d) {
                return d.Year + 1
            }),
        ] as any)
        y.domain(
            d3.extent(newData, function(d) {
                return d.Time
            }) as any
        )

        svg.append('g')
            .attr('class', 'x axis')
            .attr('id', 'x-axis')
            .attr('transform', 'translate(0,' + height + ')')
            .call(xAxis)
            .append('text')
            .attr('class', 'x-axis-label')
            .attr('x', width)
            .attr('y', -6)
            .style('text-anchor', 'end')
            .text('Year')

        svg.append('g')
            .attr('class', 'y axis')
            .attr('id', 'y-axis')
            .call(yAxis)
            .append('text')
            .attr('class', 'label')
            .attr('transform', 'rotate(-90)')
            .attr('y', 6)
            .attr('dy', '.71em')
            .style('text-anchor', 'end')
            .text('Best Time (minutes)')

        svg.append('text')
            .attr('transform', 'rotate(-90)')
            .attr('x', -160)
            .attr('y', -44)
            .style('font-size', 18)
            .text('Time')

        svg.selectAll('.dot')
            .data(newData)
            .enter()
            .append('circle')
            .attr('class', 'dot')
            .attr('r', 6)
            .attr('cx', function(d) {
                return x(d.Year)
            })
            .attr('cy', function(d) {
                return y(d.Time)
            })
            .attr('data-xvalue', function(d) {
                return d.Year
            })
            .attr('data-yvalue', function(d) {
                return d.Time.toISOString()
            })
            .on('mouseover', function(d) {
                div.style('opacity', 0.9)
                div.attr('data-year', d.Year)
                div.html(
                    d.Name +
                        ': ' +
                        d.Nationality +
                        '<br/>' +
                        'Year: ' +
                        d.Year +
                        ', Time: ' +
                        timeFormat(d.Time) +
                        (d.Doping ? '<br/><br/>' + d.Doping : '')
                )
                    .style('left', d3.event.pageX + 'px')
                    .style('top', d3.event.pageY - 28 + 'px')
            })
            .on('mouseout', function(d) {
                div.style('opacity', 0)
            })

        svg.append('text')
            .attr('id', 'title')
            .attr('x', width / 2)
            .attr('y', 0 - margin.top / 2)
            .attr('text-anchor', 'middle')
            .style('font-size', '30px')
            .text('Tour d France or what not; Doping')
            .append('text')
            .attr('x', width / 2)
            .attr('y', 0 - margin.top / 2 + 25)
            .attr('text-anchor', 'middle')
            .style('font-size', '20px')
            .append('g')
            .attr('id', 'legend')
            .selectAll('#legend')
            .data(color.domain())
            .enter()
            .append('g')
            .attr('class', 'legend-label')
            .attr('transform', function(d, i) {
                return 'translate(0,' + (height / 2 - i * 20) + ')'
            })
            .append('rect')
            .attr('x', width - 18)
            .attr('width', 18)
            .attr('height', 18)
            .style('fill', color)
            .append('text')
            .attr('x', width - 24)
            .attr('y', 9)
            .attr('dy', '.35em')
            .style('text-anchor', 'end')
            .text(function(d) {
                if (d) return 'High riders'
                else {
                    return 'Clean Riders'
                }
            })
    }

    return <div id="container" ref={d3Container}></div>
}

export default Chart
